import accountSettingsModule from '../accountSettingsModule';
import twoStepVerificationConstants from '../constants/twoStepVerificationConstants';

function enableTwoStepVerificationModalController(
  $scope,
  $uibModalInstance,
  modalData,
  twoStepVerificationService
) {
  'ngInject';

  $scope.layout = {
    password: '',
    errorMessage: '',
    debounce: false
  };

  $scope.canSubmit = () => {
    return $scope.layout.password && !$scope.layout.errorMessage && !$scope.layout.debounce;
  };

  $scope.clearErrorMessage = () => {
    $scope.layout.errorMessage = '';
  };

  $scope.dismiss = () => {
    $uibModalInstance.dismiss();
  };

  $scope.submit = form => {
    if ($scope.layout.debounce) {
      return;
    }

    if (form.$valid) {
      form.$setPristine();

      $scope.clearErrorMessage();
      $scope.layout.debounce = true;
      
      if (modalData.mediaType === twoStepVerificationConstants.mediaTypes.Email) {
        twoStepVerificationService
        .enableEmailTwoStepVerification($scope.layout.password)
          .then(() => {
            $uibModalInstance.close(true);
          })
          .catch(error => {
            $scope.layout.errorMessage = error.message;
            $scope.layout.debounce = false;
          });
      } else if (modalData.mediaType === twoStepVerificationConstants.mediaTypes.Authenticator) {
        twoStepVerificationService
          .enableAuthenticator($scope.layout.password)
          .then(data => {
            if (modalData.output) {
              modalData.output.password = $scope.layout.password;
              modalData.output.setupToken = data.setupToken;
              modalData.output.manualEntryKey = data.manualEntryKey;
              modalData.output.qrCodeImage = data.qrCodeImageUrl;
            }
            $uibModalInstance.close(true);
          })
          .catch(error => {
            $scope.layout.errorMessage = error.message;
            $scope.layout.debounce = false;
          });
      }
    }
  };
}

accountSettingsModule.controller(
  'enableTwoStepVerificationModalController',
  enableTwoStepVerificationModalController
);
export default enableTwoStepVerificationModalController;
