  $scope.getKoreaIdVerificationResult = function () {
    const {
      MDL_TOKEN,
      METHOD,
      PARENTAL_RESTRICTED_HOURS,
      PARENTAL_TIME_ZONE,
      UPDATED_TIME_ZONE
    } = screentimeConstantsService.localStorageConstants;
    const mdlToken = localStorageService.getLocalStorage(MDL_TOKEN);

    if (mdlToken) {
      const method = localStorageService.getLocalStorage(METHOD);

      koreaIdVerificationService.beginGetKoreaIdVerificationResult(mdlToken, method).then(
        result => {
          const { age } = result.data.information;
          if (age >= 18) {
            const { screentime } = $scope.parentalSettings;
            screentime.koreanIdVerified = true;
            screentime.parentalTimezone = localStorageService.getLocalStorage(PARENTAL_TIME_ZONE);
            screentime.parentalRestrictedHours = JSON.parse(
              localStorageService.getLocalStorage(PARENTAL_RESTRICTED_HOURS)
            );

            const updatedTimezone = JSON.parse(localStorageService.getLocalStorage(UPDATED_TIME_ZONE));
            $scope.saveRestrictionsHandler(updatedTimezone);
          } else {
            displayInfoModal(
              modalConstantsService.default.error.title,
              modalConstantsService.screentime.saveRestrictions.error.under18IDVerified
            );
          }
          screentimeConstantsService.localStorageCleanUp();
        },
        error => {
          console.debug(
            'Error getting korea id verification result from identity verification service',
            angular.toJson(error)
          );
          displayInfoModal(
            modalConstantsService.default.error.title,
            modalConstantsService.default.error.body
          );
        }
      );
    }
  };

