  $scope.updateRestrictionBeginTime = function (restriction, timestamp) {
    restriction.begin = timestamp;
    // Automatically set end time to 1 hr later
    // Limiting to 12AM if necessary
    const { dateTimes, generateTimestamp } = screentimeConstantsService;
    const oneHourLaterTimestamp = generateTimestamp(
      new Date(dateTimes.arbitraryDate + timestamp).getTime() + dateTimes.oneHourInMs
    );
    const endTime =
      timestamp == dateTimes.elevenPmTimestamp || timestamp == dateTimes.elevenThirtyPmTimestamp
        ? dateTimes.twentyFourHourTimestamp
        : oneHourLaterTimestamp;
    $scope.updateRestrictionEndTime(restriction, endTime);
  };

  $scope.updateRestrictionEndTime = function (restriction, timestamp) {
    restriction.end = timestamp;
  };

  $scope.toggleRestrictionDay = function (restriction, day) {
    const index = restriction.days.indexOf(day);
    if (index != -1) {
      restriction.days.splice(index, 1);
    } else {
      restriction.days.push(day);
      restriction.days.sort();
    }
  };

  $scope.dayString = function (days) {
    const { everyday, every } = screentimeConstantsService.dropdownText;
    switch (days.length) {
      case 0:
        return '';
      case 7:
        return everyday;
      default:
        var dayString = every;
        days.forEach((day, i) => {
          dayString += $scope.populateAccount.dayMappings.get(day);
          if (i != days.length - 1) {
            dayString += ', ';
          }
        });
        return dayString;
    }
  };

