  $scope.saveRestrictionsHandler = function (updatedTimezone = false) {
    if (!validateRestrictions()) {
      return;
    }

    const {
      isKoreaScreentimeEnabled,
      deviceTimezone,
      koreanIdVerified,
      parentalTimezone: originalParentalTimezone
    } = $scope.parentalSettings.screentime;

    if (updatedTimezone) {
      $scope.parentalSettings.screentime.parentalTimezone = deviceTimezone;
    }

    if ($scope.pinIsEnabledAndLocked()) {
      // Reset timezone, it will be updated again once the pin modal callbacks this function.
      $scope.parentalSettings.screentime.parentalTimezone = originalParentalTimezone;
      if (updatedTimezone) {
        $scope.unlockAccountPinSetting(
          $scope.updateTimezoneAndSaveRestrictions,
          retryPinUnlockLimit
        );
      } else {
        $scope.unlockAccountPinSetting($scope.saveRestrictionsHandler, retryPinUnlockLimit);
      }
      return;
    }

    if (isKoreaScreentimeEnabled && !koreanIdVerified) {
      // Reset timezone, it will be updated again once the ID verification completes.
      $scope.parentalSettings.screentime.parentalTimezone = originalParentalTimezone;
      $scope.launchKoreanIdVerificationModal(updatedTimezone);
      return;
    }

    removeDuplicateRestrictions();
    const { parentalRestrictedHours, parentalTimezone } = $scope.parentalSettings.screentime;
    accountsService.beginSaveRestrictedHours(parentalTimezone, parentalRestrictedHours).then(
      function (result) {
        displayInfoModal(
          modalConstantsService.default.success.title,
          modalConstantsService.default.success.body
        );
        if (updatedTimezone) {
          displayInfoModal(
            modalConstantsService.default.success.title,
            modalConstantsService.screentime.updatedTimezone.success.body
          );
        }
      },
      function (error) {
        displayInfoModal(
          modalConstantsService.default.error.title,
          modalConstantsService.default.error.body
        );
        if (updatedTimezone) {
          // Reset timezone to original timezone if restriction save failed
          $scope.parentalSettings.screentime.parentalTimezone = originalParentalTimezone;
          displayInfoModal(
            modalConstantsService.default.error.title,
            modalConstantsService.screentime.updatedTimezone.error.body
          );
        }
      }
    );
  };

  function removeDuplicateRestrictions() {
    const set = new Set();
    $scope.parentalSettings.screentime.parentalRestrictedHours.forEach(r => {
      r.days.sort();
      set.add(angular.toJson(r));
    });
    $scope.parentalSettings.screentime.parentalRestrictedHours = Array.from(set).map(r =>
      JSON.parse(r)
    );
  }

  function validateRestrictions() {
    const { parentalRestrictedHours } = $scope.parentalSettings.screentime;
    if (!parentalRestrictedHours) {
      displayInfoModal(
        modalConstantsService.default.error.title,
        modalConstantsService.default.error.body
      );
      return false;
    } else {
      for (let i = 0; i < parentalRestrictedHours.length; i++) {
        const restriction = parentalRestrictedHours[i];
  
        // Must select at least 1 day
        if (restriction.days.length == 0) {
          displayInfoModal(
            modalConstantsService.default.error.title,
            modalConstantsService.screentime.saveRestrictions.error.missingDaySelection
          );
          return false;
        }
  
        // Blocked Until time is before Blocked From time
        if (restriction.begin >= restriction.end) {
          displayInfoModal(
            modalConstantsService.default.error.title,
            modalConstantsService.screentime.saveRestrictions.error.blockedTimesWrongOrder
          );
          return false;
        }
  
        // Must have at least 1 hour playtime allowed
        const { arbitraryDate, twentyThreeHoursInMs } = screentimeConstantsService.dateTimes;
        const restrictionLengthInMs =
          new Date(arbitraryDate + restriction.end) - new Date(arbitraryDate + restriction.begin);
        if (restrictionLengthInMs > twentyThreeHoursInMs) {
          displayInfoModal(
            modalConstantsService.default.error.title,
            modalConstantsService.screentime.saveRestrictions.error.atLeastOneHourAllowed
          );
          return false;
        }
      }
      return true;
    }
  }

  $scope.onContactSettingsMasterSettingChange = function () {
    if ($scope.pinIsEnabledAndLocked()) {
      $scope.unlockAccountPinSetting(
        $scope.onContactSettingsMasterSettingChange,
        retryPinUnlockLimit
      );
      return;
    }

    const value = $scope.contactSettings.MasterSetting;
    if (value === defaultText) {
      $scope.contactSettings.PrivateMessagePrivacy = friendsText;
      $scope.contactSettings.AppChatPrivacy = friendsText;
      $scope.contactSettings.GameChatPrivacy = allUsersText;
    } else if (value === offText) {
      $scope.contactSettings.PrivateMessagePrivacy = noOneText;
      $scope.contactSettings.AppChatPrivacy = noOneText;
      $scope.contactSettings.GameChatPrivacy = noOneText;
    }
    if (value !== customText) {
      const appChatPrivacyUpdateRequest = accountsService.beginUpdateAppChatPrivacy(
        $scope.contactSettings
      );
      appChatPrivacyUpdateRequest.then(
        function (result) {},
        function (error) {
          $log.debug('Error updating app chat privacy');
        }
      );

      const gameChatPrivacyRequest = accountsService.beginUpdateGameChatPrivacy(
        $scope.contactSettings
      );
      gameChatPrivacyRequest.then(
        function (result) {},
        function (error) {
          $log.debug('Error updating game chat privacy');
        }
      );

      const privateMessagePrivacyRequest = accountsService.beginUpdatePrivateMessagePrivacy(
        $scope.contactSettings
      );
      privateMessagePrivacyRequest.then(
        function (result) {},
        function (error) {
          $log.debug('Error updating private message privacy');
        }
      );

      const promises = $q.all([
        appChatPrivacyUpdateRequest,
        gameChatPrivacyRequest,
        privateMessagePrivacyRequest
      ]);
      promises.then(
        function () {
          $scope.showSuccess();
        },
        function () {
          $log.debug('Error switching master contact setting');
          const unknownErrorText = languageResource.get('MessageUnknownError');
          systemFeedbackService.warning(unknownErrorText, 100, 5000);
        }
      );
      return promises;
    }
  };

