import accountSettingsModule from '../accountSettingsModule';

function accountSettingsModalChangeBirthdateController(
  $scope,
  $uibModalInstance,
  modalData
) {
  'ngInject';

  $scope.layout = {
    password: '',
    errorMessage: '',
    debounce: false
  };

  $scope.canSubmit = () => {
    return $scope.layout.password && !$scope.layout.errorMessage && !$scope.layout.debounce;
  };

  $scope.clearErrorMessage = () => {
    $scope.layout.errorMessage = '';
  };

  $scope.dismiss = () => {
    $uibModalInstance.dismiss();
  };

  $scope.submit = form => {
    if ($scope.layout.debounce) {
      return;
    }

    if (form.$valid) {
      form.$setPristine();

      $scope.clearErrorMessage();
      $scope.layout.debounce = true;

      modalData.updateBirthdate($scope.layout.password).then(() => {
        $uibModalInstance.close(true);
      })
      .catch(error => {
        $scope.layout.errorMessage = modalData.getBirthdateErrorMessageFromCode(error);
        $scope.layout.debounce = false;
      });
    }
  };
}

accountSettingsModule.controller(
  'accountSettingsModalChangeBirthdateController',
  accountSettingsModalChangeBirthdateController
);
export default accountSettingsModalChangeBirthdateController;
