import { FormEvents, AccountIntegrityChallengeService } from 'Roblox';
import { authenticatedUser } from 'header-scripts';
import accountSettingsModule from '../accountSettingsModule';
import twoStepVerificationConstants from '../constants/twoStepVerificationConstants';
import angular from 'angular';

function twoStepVerificationController(
  twoStepVerificationService,
  modalService,
  modalConstantsService,
  accountsService,
  accountConstantsResources,
  languageResource,
  $uibModal,
  robuxSpendTwoStepResources,
  itemTradeTwoStepResources,
  systemFeedbackService
) {
  'ngInject';

  const ctrl = this;
  ctrl.numberOfFailedTwoStepVerificationChallengeAttempts = 0;
  ctrl.layout = {
    loading: false,
    loadError: '',
    showRobuxSpendTwoStepVerificationContainer: false,
    showItemTradeTwoStepVerificationContainer: false
  };

  ctrl.authenticatorDisabledHelp = {
    aTagStart: `<a href="${twoStepVerificationConstants.urls.authenticatorHelp}" target="_blank">`,
    aTagEnd: '</a>'
  };

  ctrl.metadata = {};
  ctrl.mediaTypes = twoStepVerificationConstants.mediaTypes;
  ctrl.enabledMediaTypes = [];

  ctrl.showVerifyButtonForSpend = () => {
    twoStepVerificationService
      .getStatusSpendFriction()
      .then(showFriction => {
        if (showFriction) {
          twoStepVerificationService.sendFrictionEvent(
            robuxSpendTwoStepResources.events.verificationWarningSecurityPageTriggered
          );
        }
        if (!ctrl.layout.showItemTradeTwoStepVerificationContainer) {
          ctrl.layout.showRobuxSpendTwoStepVerificationContainer = showFriction;
        }
      })
      .catch(() => {
        // If the GET Status endpoint fails, swallow the error and proceed as normal
      });
  }

  ctrl.showVerifyButtonForTrade = () => {
    twoStepVerificationService
      .getStatusTradeFriction()
      .then(showFriction => {
        if (!ctrl.layout.showRobuxSpendTwoStepVerificationContainer) {
          ctrl.layout.showItemTradeTwoStepVerificationContainer = showFriction;
        }
      })
      .catch(() => {
        // If the GET Status endpoint fails, swallow the error and proceed as normal
      });
  }

  ctrl.shouldShowRecoveryCodesSection = () => {
    return ctrl.metadata.authenticatorEnabled && ctrl.enabledMediaTypes.length > 0;
  };

  ctrl.isMediaTypeEnabled = mediaType => {
    return ctrl.enabledMediaTypes.includes(mediaType);
  };

  // TODO: change this to true when we're ready with full Authenticator rollout.
  ctrl.showEmailWarning = false;
  ctrl.areBothEmailAndAuthenticatorEnabled = () => {
    return (
      ctrl.enabledMediaTypes.includes(twoStepVerificationConstants.mediaTypes.Email) &&
      ctrl.enabledMediaTypes.includes(twoStepVerificationConstants.mediaTypes.Authenticator)
    );
  };

  ctrl.enableEmailTwoStepVerification = () => {
    let twoStepResult;

    if (ctrl.metadata.isPasswordRequiredForEnablingEmailTwoStepVerification){
      const modal = $uibModal.open({
        templateUrl:accountConstantsResources.templates.modal.enableTwoStepVerification,
        controller:accountConstantsResources.controllers.modal.enableTwoStepVerification,
        resolve: {
          modalData: {
            mediaType: ctrl.mediaTypes.Email,
          }
        }
      });
      twoStepResult = modal.result;
    } else {
      twoStepResult = twoStepVerificationService.enableEmailTwoStepVerification();
    }

    twoStepResult
      .then(() => {
        if (!ctrl.isMediaTypeEnabled(twoStepVerificationConstants.mediaTypes.Email)) {
          ctrl.enabledMediaTypes.push(twoStepVerificationConstants.mediaTypes.Email);
        }

      ctrl.showVerifyButtonForSpend();
      ctrl.showVerifyButtonForTrade();
      });
      
    if (ctrl.metadata.isPasswordRequiredForEnablingEmailTwoStepVerification){
      twoStepResult
        .catch(() => {
          // Modal closed
        });
    } else {
      twoStepResult
        .catch(error => {
          if (error.code === twoStepVerificationConstants.errorCodes.internal.invalidPassword){
            // We haven't asked for their password yet, assume this means the flag
            // is enabled in the backend and retry.
            ctrl.metadata.isPasswordRequiredForEnablingEmailTwoStepVerification = true;
            ctrl.enableEmailTwoStepVerification();
          } else {
            modalService.open({
              titleText: modalConstantsService.default.error.title,
              bodyText: error.message
            });
          }
        });
    }
  };

  ctrl.enableAuthenticator = () => {
    const modalOutput = {
      password: '',
      setupToken: '',
      qrCodeImage: '',
      manualEntryKey: ''
    };
    const onAuthenticatorEnabled = () => {
      if (!ctrl.isMediaTypeEnabled(twoStepVerificationConstants.mediaTypes.Authenticator)) {
        ctrl.enabledMediaTypes.push(twoStepVerificationConstants.mediaTypes.Authenticator);
        ctrl.generateRecoveryCodes(false, modalOutput.password);
      }

      ctrl.showVerifyButtonForSpend();
      ctrl.showVerifyButtonForTrade();
    };

    if (ctrl.metadata.isPasswordRequiredForEnablingAuthenticator){
      const enableModal = $uibModal.open({
        templateUrl:accountConstantsResources.templates.modal.enableTwoStepVerification,
        controller:accountConstantsResources.controllers.modal.enableTwoStepVerification,
        resolve: {
          modalData: {
            mediaType: ctrl.mediaTypes.Authenticator,
            output: modalOutput
          }
        }
      });

      enableModal.result.then(() =>{
        const modal = $uibModal.open({
          templateUrl: accountConstantsResources.templates.modal.enableAuthenticator,
          controller: accountConstantsResources.controllers.modal.enableAuthenticator,
          resolve: {
            modalData: {
              metadata: ctrl.metadata,
              output: modalOutput,
              password: modalOutput.password,
              setupToken: modalOutput.setupToken,
              qrCodeImage: modalOutput.qrCodeImage,
              manualEntryKey: modalOutput.manualEntryKey
            }
          }
        });

        modal.result
          .then(onAuthenticatorEnabled)
          .catch(() => {
            // Modal closed
          });
      })
      .catch(() => {
        // Modal closed
      });
    } else {
      const modal = $uibModal.open({
        templateUrl: accountConstantsResources.templates.modal.enableAuthenticator,
        controller: accountConstantsResources.controllers.modal.enableAuthenticator,
        resolve: {
          modalData: {
            metadata: ctrl.metadata,
            output: modalOutput
          }
        }
      });

      modal.result
        .then(onAuthenticatorEnabled)
        .catch(() => {
          // Modal closed
        });
    }
  };

  ctrl.toggleTwoStepVerification = (mediaType, pinUnlocked) => {
    const currentlyEnabled = ctrl.isMediaTypeEnabled(mediaType);
    if (FormEvents) {
      if (currentlyEnabled) {
        // Disabling
        accountsService.sendButtonClickEvent(accountConstantsResources.events.toggle2SVOff);
      } else {
        // Enabling
        accountsService.sendButtonClickEvent(accountConstantsResources.events.toggle2SVOn);
      }
    }

    if (ctrl.pinLocked && !pinUnlocked) {
      // The component binding won't update immediately, so we pass pinUnlocked = true to tell the method
      // the pin has been unlocked, even if the ctrl.pinLocked hasn't been updated yet
      ctrl.unlockPin(() => ctrl.toggleTwoStepVerification(mediaType, true));
      return;
    }

    if (currentlyEnabled) {
      // Disable two step verification
      const modal = $uibModal.open({
        templateUrl: accountConstantsResources.templates.modal.twoStepSet,
        controller: accountConstantsResources.controllers.modal.twoStepSet,
        resolve: {
          modalData: {
            mediaType,
            enabledMediaTypes: ctrl.enabledMediaTypes
          }
        }
      });

      modal.result
        .then(() => {
          // Modal completed - media type was disabled.
          const mediaTypeIndex = ctrl.enabledMediaTypes.indexOf(mediaType);
          if (mediaTypeIndex >= 0) {
            ctrl.enabledMediaTypes.splice(mediaTypeIndex, 1);
          }
        })
        .catch(() => {
          // Modal closed
        });
    } else if (ctrl.emailStatus === accountConstantsResources.emailStatus.verified) {
      // Enable two step verification
      switch (mediaType) {
        case twoStepVerificationConstants.mediaTypes.Email:
          ctrl.enableEmailTwoStepVerification();
          break;
        case twoStepVerificationConstants.mediaTypes.Authenticator:
          ctrl.enableAuthenticator();
          break;
        default:
        // Can't enable unknown media type.
      }
    } else {
      modalService.open({
        titleText: modalConstantsService.emailSet.emailRequiredMessage,
        bodyText: modalConstantsService.twoStepVerification.missingEmail
      });
    }
  };

  ctrl.generateRecoveryCodes = (pinUnlocked, password) => {
    if (ctrl.pinLocked && !pinUnlocked) {
      // The component binding won't update immediately, so we pass pinUnlocked = true to tell the method
      // the pin has been unlocked, even if the ctrl.pinLocked hasn't been updated yet
      ctrl.unlockPin(() => ctrl.generateRecoveryCodes(true, password));
      return;
    }

    const modal = $uibModal.open({
      templateUrl: accountConstantsResources.templates.modal.recoveryCodes,
      controller: accountConstantsResources.controllers.modal.recoveryCodes,
      // The backdrop parameter prevents the modal from closing when its background is clicked.
      backdrop: 'static',
      // The keyboard parameter prevents the modal from closing when the escape key is pressed
      keyboard: false,
      resolve : {
        modalData: {
          password
        }
      }
    });

    modal.result
      .then(() => {
        // Modal closed, user confirmed codes saved
      })
      .catch(() => {
        // Modal dismissed
      })
      .finally(() => {
        twoStepVerificationService
          .getRecoveryCodesStatus()
          .then(data => {
            ctrl.activeRecoveryCodeCount = data.activeCount;
            ctrl.recoveryCodeCreated = data.created;
          })
          .catch(() => {
            ctrl.activeRecoveryCodeCount = null;
            ctrl.recoveryCodeCreated = null;
          });
      });
  };

  ctrl.showErrorBanner = () => {
    const errorMessage = languageResource.get('Response.VerificationError');
    systemFeedbackService.warning(errorMessage, 100, 6000);
  };

  ctrl.receiveTwoStepChallengeTokenForSpend = tokenData => {
    twoStepVerificationService
      .redeemVerificationChallengeForSpend(ctrl.challengeToken, tokenData.verificationToken)
      .then(success => {
        if (success) {
          const successMessage = languageResource.get('Response.SuccessfulVerificationV2');
          systemFeedbackService.success(successMessage, 100, 6000);
          ctrl.layout.showRobuxSpendTwoStepVerificationContainer = false;
          ctrl.layout.showItemTradeTwoStepVerificationContainer = false;
        } else {
          // Redeem failed. Show generic error failed message.
          ctrl.showErrorBanner();
        }
      })
      .catch(() => {
        ctrl.showErrorBanner();
      });
  };

  ctrl.receiveTwoStepChallengeTokenForTrade = tokenData => {
    twoStepVerificationService
      .redeemVerificationChallengeForTrade(ctrl.challengeToken, tokenData.verificationToken)
      .then(success => {
        if (success) {
          const successMessage = languageResource.get('Response.SuccessfulVerificationV2');
          systemFeedbackService.success(successMessage, 100, 6000);
          ctrl.layout.showRobuxSpendTwoStepVerificationContainer = false;
          ctrl.layout.showItemTradeTwoStepVerificationContainer = false;
        } else {
          // Redeem failed. Show generic error failed message.
          ctrl.showErrorBanner();
        }
      })
      .catch(() => {
        ctrl.showErrorBanner();
      });
  };

  ctrl.handleInvalidatedTwoStepVerificationChallengeForSpend = () => {
    const MAX_RETRY_ATTEMPTS = 3;
    // Allow the user to try again once if the session is invalidated. If this doesn't work don't show again.
    if (ctrl.numberOfFailedTwoStepVerificationChallengeAttempts < MAX_RETRY_ATTEMPTS) {
      ctrl.renderTwoStepVerificationChallengeForSpend();
    }
    else {
      ctrl.showErrorBanner();
    }
    ctrl.numberOfFailedTwoStepVerificationChallengeAttempts += 1;
  };

  ctrl.handleInvalidatedTwoStepVerificationChallengeForTrade = () => {
    const MAX_RETRY_ATTEMPTS = 3;
    // Allow the user to try again once if the session is invalidated. If this doesn't work don't show again.
    if (ctrl.numberOfFailedTwoStepVerificationChallengeAttempts < MAX_RETRY_ATTEMPTS) {
      ctrl.renderTwoStepVerificationChallengeForTrade();
    }
    else {
      ctrl.showErrorBanner();
    }
    ctrl.numberOfFailedTwoStepVerificationChallengeAttempts += 1;
  };

  ctrl.renderTwoStepVerificationChallengeForSpend = () => {
    twoStepVerificationService
      .generateChallengeForSpend()
      .then(challengeToken => {
        const { TwoStepVerification } = AccountIntegrityChallengeService;
        ctrl.challengeToken = challengeToken;
        TwoStepVerification.renderChallenge({
          containerId: '2sv-popup-container',
          userId: authenticatedUser.id,
          challengeId: challengeToken,
          actionType: TwoStepVerification.ActionType.RobuxSpend,
          renderInline: false,
          shouldShowRememberDeviceCheckbox: false,
          shouldDynamicallyLoadTranslationResources: false,
          onChallengeCompleted: ctrl.receiveTwoStepChallengeTokenForSpend,
          onChallengeInvalidated: ctrl.handleInvalidatedTwoStepVerificationChallengeForSpend,
          onModalChallengeAbandoned: angular.noop
        });
      })
      .catch(() => {
        ctrl.showErrorBanner();
      });
  };

  ctrl.renderTwoStepVerificationChallengeForTrade = () => {
    twoStepVerificationService
      .generateChallengeForTrade()
      .then(challengeToken => {
        const { TwoStepVerification } = AccountIntegrityChallengeService;
        ctrl.challengeToken = challengeToken;
        TwoStepVerification.renderChallenge({
          containerId: '2sv-popup-container',
          userId: authenticatedUser.id,
          challengeId: challengeToken,
          actionType: TwoStepVerification.ActionType.ItemTrade,
          renderInline: false,
          shouldShowRememberDeviceCheckbox: false,
          shouldDynamicallyLoadTranslationResources: false,
          onChallengeCompleted: ctrl.receiveTwoStepChallengeTokenForTrade,
          onChallengeInvalidated: ctrl.handleInvalidatedTwoStepVerificationChallengeForTrade,
          onModalChallengeAbandoned: angular.noop
        });
      })
      .catch(() => {
        ctrl.showErrorBanner();
      });
  };

  ctrl.robuxSpendVerifyChallenge = () => {
    twoStepVerificationService.sendFrictionEvent(
      robuxSpendTwoStepResources.events.codeInputModalTriggered
    );
    twoStepVerificationService.sendFrictionEvent(
      robuxSpendTwoStepResources.events.verifySecurityPageBtn
    );

    if (ctrl.metadata.isUsingTwoStepWebviewComponent) {
      ctrl.renderTwoStepVerificationChallengeForSpend();
    } else {
      const modal = $uibModal.open({
        templateUrl: accountConstantsResources.templates.modal.robuxSpendVerifyChallenge,
        controller: accountConstantsResources.controllers.modal.robuxSpendVerifyChallenge
      });
  
      modal.result
        .then(success => {
          if (success) {
            ctrl.layout.showRobuxSpendTwoStepVerificationContainer = false;
            ctrl.layout.showItemTradeTwoStepVerificationContainer = false;
          } else {
            modalService.open({
              titleText: modalConstantsService.default.error.title,
              bodyText: modalConstantsService.default.error.body
            });
          }
        })
        .catch(() => {
          // Modal closed
        });
    }
  };

  ctrl.itemTradeVerifyChallenge = () => {
    twoStepVerificationService.sendFrictionEvent(
      itemTradeTwoStepResources.events.codeInputModalTriggered
    );
    twoStepVerificationService.sendFrictionEvent(
      itemTradeTwoStepResources.events.verifySecurityPageBtn
    );

    ctrl.renderTwoStepVerificationChallengeForTrade();
  };

  const loadError = () => {
    ctrl.layout.loadError = languageResource.get('Response.GeneralError');
    ctrl.layout.loading = false;
  };

  const init = () => {
    ctrl.layout.loading = true;

    twoStepVerificationService
      .getMetadata()
      .then(metadata => {
        ctrl.metadata = metadata;

        if (metadata.twoStepVerificationEnabled) {
          twoStepVerificationService
            .getTwoStepVerificationConfiguration()
            .then(configuration => {
              const enabledMediaTypes = [];

              configuration.methods.forEach(method => {
                if (method.enabled && twoStepVerificationConstants.mediaTypes[method.mediaType]) {
                  enabledMediaTypes.push(method.mediaType);
                }
              });

              ctrl.enabledMediaTypes = enabledMediaTypes;
              ctrl.layout.loading = false;
            })
            .catch(loadError);

          ctrl.showVerifyButtonForSpend();
          ctrl.showVerifyButtonForTrade();
        } else {
          ctrl.layout.loadError = languageResource.get('Response.FeatureDisabled');
          ctrl.layout.loading = false;
        }

        if (metadata.authenticatorEnabled) {
          twoStepVerificationService
            .getRecoveryCodesStatus()
            .then(data => {
              ctrl.activeRecoveryCodeCount = data.activeCount;
              ctrl.recoveryCodeCreated = data.created;
            })
            .catch(() => {
              ctrl.activeRecoveryCodeCount = null;
              ctrl.recoveryCodeCreated = null;
            });
        }
      })
      .catch(loadError);
  };

  ctrl.$onInit = init;
}

accountSettingsModule.controller('twoStepVerificationController', twoStepVerificationController);
export default twoStepVerificationController;
