import accountSettingsModule from '../accountSettingsModule';

function accountsController(
  $scope,
  $state,
  $log,
  notificationConstantsService,
  accountsService,
  commonConstantsService,
  accountConstantsResources,
  screentimeConstantsService
) {
  'ngInject';

  $scope.accountsTabs = [];

  $scope.currentData = {
    activeTab: '',
    stateLabel: ''
  };

  $scope.initTabs = function () {
    $scope.accountsTabs = accountConstantsResources.accountTabsDefault;
    accountsService.getGroupsAndPolicy().then(
      function (result) {
        $scope.accountsTabs = [];
        for (let i = 0; i < result.tabsListing.length; i++) {
          if (result.getPolicy.displayBillingTab || result.tabsListing[i].suffix !== 'billing') {
            $scope.accountsTabs.push({
              name: result.tabsListing[i].suffix,
              label: result.tabsListing[i].title
            });
          }
        }
      },
      function (err) {
        $log.debug('Error getting account info.');
      }
    );
  };

  // Check if this user has any configurable notification types
  $scope.mappedDestinationTypes = [];
  $scope.destinationTypes = notificationConstantsService.destinationTypes;

  const getAllowedNotificationDestinationTypes = function () {
    accountsService.beginGetAllowedNotificationDestinationTypes().then(
      function (allowedDestinationTypes) {
        if (allowedDestinationTypes && allowedDestinationTypes.length > 0) {
          $scope.mappedDestinationTypes = $scope.destinationTypes.filter(function (
            destinationType
          ) {
            return allowedDestinationTypes.indexOf(destinationType) !== -1;
          });
        }
      },
      function (err) {
        $log.debug('Error getting account info');
      }
    );
  };

  accountsService.beginGetAccountInformationMetadata().then(
    function (result) {
      if (!result.isAllowedNotificationsEndpointDisabled) {
        this.getAllowedNotificationDestinationTypes();
      }
    },
    function (err) {
      this.getAllowedNotificationDestinationTypes();
    }
  );

  $scope.accountData = !$scope.accountData
    ? {
        stateProperties: {}
      }
    : $scope.accountData;

  $scope.$on('$stateChangeSuccess', function (event, toState, toParams, fromState, fromParams) {
    $scope.currentData.activeTab = toState.name;
    $scope.currentData.stateLabel = commonConstantsService.tabs[toState.name];
    if (toState.name === accountConstantsResources.accountTabsNames.parentalControlsCheckStatus) {
      $scope.currentData.activeTab = "parental-controls"
    } else {
      screentimeConstantsService.localStorageCleanUp();
    }
  });

  $scope.$watch('accountData', function (newValue, oldValue) {
    $scope.hideMobileTabList = $scope.accountData.stateProperties.hideMobileTabList;
  });

  $scope.initTabs();
}

accountSettingsModule.controller('accountsController', accountsController);

export default accountsController;
