import { localStorageService } from 'core-roblox-utilities';
import { Intl } from 'Roblox';
import accountSettingsModule from '../accountSettingsModule';

function screentimeConstantsService($log, languageResource) {
  'ngInject';

  const intl = Intl && new Intl();
  const ls = languageResource;
  const dateTimes = {
    thirtyMinutesInMs: 1800000,
    oneHourInMs: 3600000,
    twentyThreeHoursInMs: 82800000,
    arbitraryDate: '01/01/2000 ',
    zeroHourTimestamp: '00:00:00',
    twentyFourHourTimestamp: '24:00:00',
    elevenThirtyPmTimestamp: '23:30:00',
    elevenPmTimestamp: '23:00:00'
  };

  const localStorageConstants = {
    MDL_TOKEN: 'mdlTkn',
    METHOD: 'method',
    PARENTAL_RESTRICTED_HOURS: 'parentalRestrictedHours',
    PARENTAL_TIME_ZONE: 'parentalTimeZone',
    UPDATED_TIME_ZONE: 'updatedTimeZone'
  };

  const generateTimestamp = time => {
    const date = new Date(time);
    const [hour, minutes] = [date.getHours(), date.getMinutes()];
    const hourPaddedByZero = hour < 10 ? '0' + hour : hour;
    const minutesPaddedByZero = minutes < 10 ? '0' + minutes : minutes;
    return hourPaddedByZero + ':' + minutesPaddedByZero + ':00';
  }

  const generateTimestampsInBetween = (start, end) => {
    var timestamps = [start];
    var startTime = new Date(dateTimes.arbitraryDate + start).getTime();
    var endTime = new Date(dateTimes.arbitraryDate + end).getTime();
    for (let i = startTime + dateTimes.thirtyMinutesInMs; i < endTime; i += dateTimes.thirtyMinutesInMs) {
      timestamps.push(generateTimestamp(i));
    }
    timestamps.push(end);
    return timestamps;
  };

  const self = {
    defaultRestriction: {
      begin: '00:00:00',
      end: '06:00:00',
      days: [0, 1, 2 ,3, 4, 5, 6]
    },

    dropdownText: {
      everyday: ls.get('Label.Everyday'),
      every: ls.get('Label.Every')
    },

    verificationOptions: [
      { label: ls.get('Action.BankAccount'), value: 'card' },
      { label: ls.get('Action.Phone'), value: 'phone' },
      { label: ls.get('Action.IPIN'), value: 'ipin' }
    ],

    dateTimes: dateTimes,
    generateTimestamp: generateTimestamp,
    localStorageConstants: localStorageConstants,

    generateTimeMappings() {
      const timeMappings = new Map();
      var times = generateTimestampsInBetween(dateTimes.zeroHourTimestamp, dateTimes.twentyFourHourTimestamp);
      for (let i = 0; i < times.length; i++) {
        let timestamp = times[i];
        const timeFormatted = intl.getDateTimeFormatter().getCustomDateTime(
                                new Date(
                                  dateTimes.arbitraryDate + timestamp), 
                                  { timeStyle: "short" }
                                );
        timeMappings.set(timestamp, timeFormatted);
      }
      //Map 24:00:00 to 12:00 AM
      timeMappings.set(dateTimes.twentyFourHourTimestamp, timeMappings.get(dateTimes.zeroHourTimestamp));
      return timeMappings;
    },

    generateDayMappings(weekdaysList) {
      const dayMappings = new Map();
      //Add Sunday first since getWeekdaysList() returns Sunday==7, but we want Sunday == 0
      dayMappings.set(0, weekdaysList[6].name);
      for (let i = 0; i < 6; i++) {
        dayMappings.set(weekdaysList[i].value, weekdaysList[i].name);
      }
      return dayMappings;
    },

    generateRestrictionBeginTimes(timeMappings, curfewRestrictions = []) {
      const restrictionBeginTimes = [];
      var invalidTimes = new Set();
      for (let i = 0; i < curfewRestrictions.length; i++) {
        let restriction = curfewRestrictions[i];
        let curfewTimestamps = generateTimestampsInBetween(restriction.begin, restriction.end);
        curfewTimestamps.pop(); //Remove the last time, as we can add restrictions starting at that time
        curfewTimestamps.forEach(time => invalidTimes.add(time));
      }

      // 24:00:00 is not a valid begin time
      generateTimestampsInBetween(dateTimes.zeroHourTimestamp, dateTimes.elevenThirtyPmTimestamp).forEach(timestamp => {
        if (!invalidTimes.has(timestamp)) {
          restrictionBeginTimes.push({
            timestamp: timestamp,
            name: timeMappings.get(timestamp)
          }); 
        }
      });
      return restrictionBeginTimes;
    },

    generateRestrictionEndTimes(timeMappings, curfewRestrictions = []) {
      const restrictionEndTimes = [];
      var invalidTimes = new Set();
      for (let i = 0; i < curfewRestrictions.length; i++) {
        let restriction = curfewRestrictions[i];
        let curfewTimestamps = generateTimestampsInBetween(restriction.begin, restriction.end);
        curfewTimestamps.splice(0, 1); //Remove the first time, as we can add a restriction ending at that time
        curfewTimestamps.forEach(time => invalidTimes.add(time));
      }

      //00:00:00 is not a valid end time
      generateTimestampsInBetween('00:30:00', dateTimes.twentyFourHourTimestamp).forEach(timestamp => {
        if (!invalidTimes.has(timestamp)) {
          restrictionEndTimes.push({
            timestamp: timestamp,
            name: timeMappings.get(timestamp)
          }); 
        }
      });
      return restrictionEndTimes;
    },

    localStorageCleanUp(keys = []) {
      const keysToBeCleaned = keys.length > 0 ? keys : localStorageConstants;
      for (const key in keysToBeCleaned) {
        localStorageService.removeLocalStorage(localStorageConstants[key]);
      }
    }
  }

  return self;
};

accountSettingsModule.factory('screentimeConstantsService', screentimeConstantsService);

export default screentimeConstantsService;
