import accountSettingsModule from '../accountSettingsModule';
import twoStepVerificationConstants from '../constants/twoStepVerificationConstants';

function robuxSpendVerifyChallengeModalController(
  $scope,
  $uibModalInstance,
  twoStepVerificationService,
  urlService,
  robuxSpendTwoStepResources,
  languageResource,
  systemFeedbackService
) {
  'ngInject';

  $scope.layout = {
    codeSent: false,
    isPendingVerification: false,
    invalidCodeError: false,
    invalidCodeErrorMessage: '',
    supportPageUrl: urlService.getAbsoluteUrl(robuxSpendTwoStepResources.urls.support),
    supportPageLinkPhrase: '',
    codeInputPlaceholder: languageResource.get('Label.CodeInputPlaceholderText', {
      codeLength: robuxSpendTwoStepResources.codeLength
    }),
    codeLength: robuxSpendTwoStepResources.codeLength,
    isChallengeVerified: false
  };

  $scope.properties = {
    challengeToken: '',
    code: ''
  };

  function getSupportLinkPhrase(supportUrl) {
    const supportPageLink = `<a class='text-name text-footer contact' href='${supportUrl}' target='_blank'>${languageResource.get(
      'Label.RobloxSupport'
    )}</a>`;
    return languageResource.get('Label.NeedHelpContactSupport', { supportLink: supportPageLink });
  }

  function showErrorBanner() {
    $scope.dismiss();
    const errorMessage = languageResource.get('Response.VerificationError');
    systemFeedbackService.warning(errorMessage, 100, 6000);
  }

  function showModalError(code) {
    let errorMessage = languageResource.get('Response.VerificationError');
    if (code === twoStepVerificationConstants.errorCodes.internal.invalidCode) {
      errorMessage = languageResource.get('Response.InvalidCodeTryAgain');
    }
    $scope.layout.invalidCodeError = true;
    $scope.layout.invalidCodeErrorMessage = errorMessage;
    twoStepVerificationService.sendFrictionEvent(
      robuxSpendTwoStepResources.events.invalidCodeInput
    );
  }

  $scope.dismiss = () => {
    if ($scope.layout.isChallengeVerified) {
      const successMessage = languageResource.get('Response.SuccessfulVerificationV2');
      systemFeedbackService.success(successMessage, 100, 6000);
      $uibModalInstance.close(true);
    } else {
      $uibModalInstance.dismiss();
    }
  };

  $scope.resendCode = function () {
    twoStepVerificationService.sendFrictionEvent(robuxSpendTwoStepResources.events.resendCodeBtn);

    twoStepVerificationService
      .resendEmailCode($scope.properties.challengeToken)
      .then(() => {
        $scope.layout.codeSent = true;
      })
      .catch(err => {
        showModalError(err.code);
      });
  };

  $scope.verifyChallengeCode = function () {
    twoStepVerificationService.sendFrictionEvent(robuxSpendTwoStepResources.events.verifyCodeBtn);

    $scope.layout.isPendingVerification = true;
    twoStepVerificationService
      .verifyChallengeCode($scope.properties.challengeToken, $scope.properties.code)
      .then(verificationToken => {
        twoStepVerificationService.sendFrictionEvent(
          robuxSpendTwoStepResources.events.successfulVerification
        );
        $scope.layout.isChallengeVerified = true;
        twoStepVerificationService
          .redeemVerificationChallengeForSpend($scope.properties.challengeToken, verificationToken)
          .then(success => {
            if (success) {
              $scope.dismiss();
            } else {
              // Redeem failed. Show generic error failed message.
              showErrorBanner();
            }
          })
          .catch(() => {
            showErrorBanner();
          });
      })
      .catch(err => {
        showModalError(err.code);
        $scope.layout.isPendingVerification = false;
      });
  };

  $scope.generateChallengeCode = function () {
    $scope.layout.supportPageLinkPhrase = getSupportLinkPhrase($scope.layout.supportPageUrl);

    twoStepVerificationService
      .generateChallengeForSpend()
      .then(challengeToken => {
        $scope.properties.challengeToken = challengeToken;
      })
      .catch(() => {
        showErrorBanner();
      });
  };

  $scope.generateChallengeCode();
}

accountSettingsModule.controller(
  'robuxSpendVerifyChallengeModalController',
  robuxSpendVerifyChallengeModalController
);
export default robuxSpendVerifyChallengeModalController;