import accountSettingsModule from '../accountSettingsModule';
import twoStepVerificationConstants from '../constants/twoStepVerificationConstants';

function enableAuthenticatorModalController(
  $scope,
  $uibModalInstance,
  modalData,
  twoStepVerificationService
) {
  'ngInject';

  $scope.layout = {
    code: '',
    setupToken: modalData.setupToken || '',
    qrCodeImage: modalData.qrCodeImage || '',
    manualEntryKey: modalData.manualEntryKey || '',
    errorMessage: '',
    passwordErrorMessage: '',
    password: modalData.password || '',
    debounce: false,
    viewQrCode: true,
    authenticatorCodeLength: modalData.metadata.authenticatorCodeLength,
    helpLink: modalData.metadata.authenticatorHelpSiteAddress,
    passwordRequired: modalData.metadata.isPasswordRequiredForEnablingAuthenticator && !modalData.password
  };

  $scope.canVerify = () => {
    return (
      !$scope.layout.errorMessage &&
      !$scope.layout.debounce &&
      $scope.layout.code &&
      $scope.layout.code.length === modalData.metadata.authenticatorCodeLength &&
      (!$scope.layout.passwordRequired || $scope.layout.password)
    );
  };

  $scope.clearErrorMessage = () => {
    $scope.layout.errorMessage = '';
  };

  $scope.clearPasswordErrorMessage = () => {
    $scope.layout.passwordErrorMessage = '';
  };

  $scope.dismiss = () => {
    $uibModalInstance.dismiss();
  };

  $scope.onKeyDown = $event => {
    if (!twoStepVerificationConstants.digitRegex.test($event.key)) {
      $event.originalEvent.preventDefault();
    }
  };

  $scope.toggleQrCode = () => {
    $scope.layout.viewQrCode = !$scope.layout.viewQrCode;
  };

  $scope.openHelpLink = () => {
    window.open($scope.layout.helpLink);
  };

  $scope.submit = form => {
    if ($scope.layout.debounce) {
      return;
    }

    if (form.$valid) {
      form.$setPristine();

      $scope.clearErrorMessage();
      $scope.layout.debounce = true;

      twoStepVerificationService
        .enableVerifyAuthenticator($scope.layout.setupToken, $scope.layout.code, $scope.layout.password)
        .then(() => {
          modalData.output.password = $scope.layout.password;
          $uibModalInstance.close(true);
        })
        .catch(error => {
          if (error.code == twoStepVerificationConstants.errorCodes.internal.invalidPassword){
            $scope.layout.passwordRequired = true;
            $scope.layout.passwordErrorMessage = error.message;
          } else {
            $scope.layout.errorMessage = error.message;
          }
          $scope.layout.debounce = false;
        });
    }
  };

  if (!modalData.metadata.isPasswordRequiredForEnablingAuthenticator){
    twoStepVerificationService
      .enableAuthenticator($scope.layout.password)
      .then(data => {
        $scope.layout.setupToken = data.setupToken;
        $scope.layout.manualEntryKey = data.manualEntryKey;
        $scope.layout.qrCodeImage = data.qrCodeImageUrl;
      })
      .catch(err => {
        $scope.layout.errorMessage = err.message;
      });
  }
}

accountSettingsModule.controller(
  'enableAuthenticatorModalController',
  enableAuthenticatorModalController
);
export default enableAuthenticatorModalController;
