import { EnvironmentUrls } from 'Roblox';
import { urlService, httpService } from 'core-utilities';
import accountSettingsModule from '../accountSettingsModule';

function koreaIdVerificationService($log, languageResource, screentimeConstantsService) {
  'ngInject';
  
  const { websiteUrl, apiGatewayUrl } = EnvironmentUrls;
  const accountSettingsPath = '/my/account#!/parental-controls/check-status';
  const startKoreaIdVerificationUrl = `${apiGatewayUrl}/identity-verification-service/v1/korea/start-verification`;
  const getKoreaIdVerificationResultUrl = `${apiGatewayUrl}/identity-verification-service/v1/korea/get-verification-result`;

  const getCallBackUrl = () => {
    return urlService.resolveUrl(websiteUrl, accountSettingsPath);
  }

  const self = {

    formPostHelper(path, params, method = 'post') {
      const form = document.createElement('form');
      form.method = method;
      form.action = path;
    
      Object.keys(params).forEach(key => {
        if (Object.prototype.hasOwnProperty.call(params, key)) {
          const hiddenField = document.createElement('input');
          hiddenField.type = 'hidden';
          hiddenField.name = key;
          hiddenField.value = params[key];
    
          form.appendChild(hiddenField);
        }
      });
      return form;
    },

    beginStartKoreaIdVerification(method) {
      const urlConfig = {
        withCredentials: true,
        url: startKoreaIdVerificationUrl
      };
      const params = {
        method,
        callbackUrl: getCallBackUrl()
      }
      return httpService.post(urlConfig, params);
    },
    
    beginGetKoreaIdVerificationResult(mdlToken, method) {
      const urlConfig = {
        withCredentials: true,
        url: getKoreaIdVerificationResultUrl
      };
      const params = {
        method: method,
        mdlToken: mdlToken
      };
      return httpService.post(urlConfig, params);
    }
  }

  return self;
};

accountSettingsModule.factory('koreaIdVerificationService', koreaIdVerificationService);

export default koreaIdVerificationService;
