import { Lang } from 'Roblox';
import angular from 'angular';

const dependencies = [
  'robloxApp',
  'ui.router',
  'cursorPagination',
  'premium',
  'systemFeedback',
  'verticalMenu',
  'modal',
  'phoneValidation'
];
const accounts = angular.module('accounts', dependencies).config([
  '$stateProvider',
  '$urlRouterProvider',
  '$locationProvider',
  'accountConstantsResources',
  'robuxSpendTwoStepResources',
  'itemTradeTwoStepResources',
  'languageResourceProvider',
  (
    $stateProvider,
    $urlRouterProvider,
    $locationProvider,
    accountConstantsResources,
    robuxSpendTwoStepResources,
    itemTradeTwoStepResources,
    languageResourceProvider
  ) => {
    // validate url
    const loc = window.location.href;
    if (loc.indexOf('#') != -1 && loc.indexOf('#!') == -1) {
      window.location.href = loc.replace('#', '#!');
    }
    // false mean hashband mode
    $locationProvider.html5Mode(false);
    $locationProvider.hashPrefix('!');
    $urlRouterProvider.otherwise('/info');
    $stateProvider
      .state('info', {
        url: '/info',
        label: 'Account Info',
        templateUrl: accountConstantsResources.templates.accountInfo
      })
      .state('social', {
        url: '/social',
        label: 'Social',
        templateUrl: accountConstantsResources.templates.accountSocial
      })
      .state('security', {
        url: '/security',
        label: 'Security',
        templateUrl: accountConstantsResources.templates.accountSecurity
      })
      .state('privacy', {
        url: '/privacy',
        label: 'Privacy',
        templateUrl: accountConstantsResources.templates.accountPrivacy
      })
      .state('parental-controls', {
        url: '/parental-controls',
        label: 'Parental Controls',
        templateUrl: accountConstantsResources.templates.accountParentalControls
      })
      .state(accountConstantsResources.accountTabsNames.parentalControlsCheckStatus, {
        url: '/parental-controls/check-status',
        label: 'Parental Controls',
        templateUrl: accountConstantsResources.templates.accountParentalControls
      })
      .state('billing', {
        url: '/billing',
        label: 'Billing',
        templateUrl: accountConstantsResources.templates.accountBilling
      })
      .state('notifications', {
        url: '/notifications',
        label: 'Notifications',
        templateUrl: accountConstantsResources.templates.accountNotifications
      })
      .state('fast-track', {
        url: '/fast-track',
        label: 'Fast Track',
        templateUrl: accountConstantsResources.templates.accountFastTrack
      });

    const accountSettingsResources = Lang.AccountSettingsResources;
    // Temporary until strings are added
    const translationConstants = {
      'Label.Notifications.GroupJoinRequestAccepted':
        (accountSettingsResources &&
          accountSettingsResources['Label.Notifications.GroupJoinRequestAccepted']) ||
        'Someone accepts my group join request',
        'Response.Dialog.EnableTwoStepVerification': (accountSettingsResources && accountSettingsResources['Response.Dialog.EnableTwoStepVerification']) || 'Turn on 2 Step Verification',
        'Response.Dialog.EnableTwoStepVerificationMessage': (accountSettingsResources && accountSettingsResources['Response.Dialog.EnableTwoStepVerificationMessage']) || 'To turn on 2-Step Verification, please verify account password.',
        'Message.RobuxSpendTwoStepVerificationMessage': (accountSettingsResources && accountSettingsResources['Message.RobuxSpendTwoStepVerificationMessage']) || 'Verification is required to continue with your virtual transaction.',
        'Message.ItemTradeTwoStepVerificationMessage': (accountSettingsResources && accountSettingsResources['Message.ItemTradeTwoStepVerificationMessage']) || 'Verification is required to continue with your trade.',
      'Label.USDCurrency': (accountSettingsResources && accountSettingsResources['Label.USDCurrency']) || 'USD',
      'Label.ChatFilteringLevel': (accountSettingsResources && accountSettingsResources['Label.ChatFilteringLevel']) || 'Chat Filtering Level',
      'Label.ToolTip.ChatFilteringLevel': (accountSettingsResources && accountSettingsResources['Label.ToolTip.ChatFilteringLevel']) ||
        'All text chat is scanned for language that is inappropriate within the Roblox community. This setting controls what you can send and receive while chatting with others on Roblox.',
      'Label.DropDown.MaximumFiltering': (accountSettingsResources && accountSettingsResources['Label.DropDown.MaximumFiltering']) || 'Maximum Filtering',
      'Label.DropDown.13PlusFiltering': (accountSettingsResources && accountSettingsResources['Label.DropDown.13PlusFiltering']) || '13+ Filtering'
    };

    languageResourceProvider.setLanguageKeysFromFile(Lang.AccountSettingsResources);
    languageResourceProvider.setLanguageKeysFromFile(translationConstants);
    languageResourceProvider.setLanguageKeysFromFile(Lang.ControlsResources);
    languageResourceProvider.setLanguageKeysFromFile(Lang['Authentication.Social']);
    languageResourceProvider.setLanguageKeysFromFile(Lang.PremiumResources);
    languageResourceProvider.setLanguageKeysFromFile(Lang.DisplayNameResources);
    languageResourceProvider.setLanguageKeysFromFile(Lang.TwoStepVerificationResources);
    languageResourceProvider.setLanguageKeysFromFile(Lang.IdVerificationResources);
  }
]);

export default accounts;
