import accountSettingsModule from '../accountSettingsModule';
import twoStepVerificationConstants from '../constants/twoStepVerificationConstants';

function recoveryCodesModalController($scope, $uibModalInstance, modalData, twoStepVerificationService) {
  'ngInject';

  $scope.layout = {
    password: modalData.password || '',
    recoveryCodes: null,
    recoveryCodesLeft: [],
    recoveryCodesRight: [],
    activeRecoveryCodeCount: 0,
    errorMessage: '',
    loadErrorMessage: '',
    loading: true,
    debounce: false,
    confirmSavedCodes: false,
    passwordRequired: !modalData.password
  };

  $scope.canContinue = () => {
    return $scope.layout.password && !$scope.layout.errorMessage && !$scope.layout.debounce;
  };

  $scope.clearErrorMessage = () => {
    $scope.layout.errorMessage = '';
  };

  $scope.dismiss = () => {
    // Close without finishing
    $uibModalInstance.dismiss();
  };

  $scope.close = () => {
    // Close after recovery codes have been saved
    $uibModalInstance.close(true);
  };

  $scope.formatRecoveryCodes = recoveryCodes => {
    return recoveryCodes.join('\n');
  };

  $scope.submit = form => {
    if ($scope.layout.debounce) {
      return;
    }

    if (form.$valid) {
      form.$setPristine();

      $scope.clearErrorMessage();
      $scope.layout.debounce = true;

      twoStepVerificationService
        .generateRecoveryCodes($scope.layout.password)
        .then(recoveryCodes => {
          $scope.layout.recoveryCodes = recoveryCodes;
          $scope.layout.recoveryCodesLeft = recoveryCodes.slice(0, recoveryCodes.length / 2);
          $scope.layout.recoveryCodesRight = recoveryCodes.slice(recoveryCodes.length / 2);
        })
        .catch(error => {
          if (error.code == twoStepVerificationConstants.errorCodes.internal.invalidPassword){
            $scope.layout.passwordRequired = true;
          }
          $scope.layout.errorMessage = error.message;
          $scope.layout.debounce = false;
        });
    }
  };

  twoStepVerificationService
    .getRecoveryCodesStatus()
    .then(data => {
      $scope.layout.activeRecoveryCodeCount = data.activeCount;
      if (!$scope.layout.passwordRequired && !$scope.layout.activeRecoveryCodeCount) {
        twoStepVerificationService
          .generateRecoveryCodes($scope.layout.password)
          .then(recoveryCodes => {
            $scope.layout.recoveryCodes = recoveryCodes;
            $scope.layout.recoveryCodesLeft = recoveryCodes.slice(0, recoveryCodes.length / 2);
            $scope.layout.recoveryCodesRight = recoveryCodes.slice(recoveryCodes.length / 2);
          })
          .catch(error => {
            if (error.code == twoStepVerificationConstants.errorCodes.internal.invalidPassword){
              $scope.layout.passwordRequired = true;
            }
            $scope.layout.errorMessage = error.message;
            $scope.layout.debounce = false;
          })
          .finally(() => {
            $scope.layout.loading = false;
          });
      } else {
        $scope.layout.loading = false;
      }
    })
    .catch(err => {
      $scope.layout.loadErrorMessage = err.message;
      $scope.layout.loading = false;
    });
}

accountSettingsModule.controller('recoveryCodesModalController', recoveryCodesModalController);
export default recoveryCodesModalController;
