import angular from 'angular';
import { localStorageService } from 'core-roblox-utilities';
import accountSettingsModule from '../accountSettingsModule';

function accountSettingsModalKoreanIdVerificationController (
  $scope,
  $log,
  modalService,
  modalData,
  koreaIdVerificationService,
  screentimeConstantsService,
  modalConstantsService
) {
  'ngInject';

  angular.extend($scope, modalData);
  
  $scope.verificationOptions = screentimeConstantsService.verificationOptions;

  $scope.verificationHandler = function(method) {
    const { MDL_TOKEN, METHOD, PARENTAL_RESTRICTED_HOURS, PARENTAL_TIME_ZONE, UPDATED_TIME_ZONE } = screentimeConstantsService.localStorageConstants;
    localStorageService.setLocalStorage(PARENTAL_RESTRICTED_HOURS, angular.toJson($scope.parentalRestrictedHours));
    localStorageService.setLocalStorage(PARENTAL_TIME_ZONE, $scope.parentalTimezone);
    localStorageService.setLocalStorage(UPDATED_TIME_ZONE, $scope.updatedTimezone);

    koreaIdVerificationService.beginStartKoreaIdVerification(method).then(
      response => {
        const { url, parameters } = response.data;
        const mdlTkn = method === 'phone' ? parameters.mdl_tkn : parameters.mdlTkn;
        localStorageService.setLocalStorage(MDL_TOKEN, mdlTkn);
        localStorageService.setLocalStorage(METHOD, method);

        // KCB can only accept request as a Form POST
        const hiddenForm = koreaIdVerificationService.formPostHelper(url, parameters);
        document.body.appendChild(hiddenForm);
        hiddenForm.submit();
      },
      error => {
        console.debug("Error starting korea id verification from identity verification service", angular.toJson(error));
        modalService.open({
          titleText: modalConstantsService.default.error.title,
          bodyText: modalConstantsService.default.error.body
        });
      }
    );
  }
}

accountSettingsModule.controller(
  'accountSettingsModalKoreanIdVerificationController',
  accountSettingsModalKoreanIdVerificationController
);

export default accountSettingsModalKoreanIdVerificationController;
