import { EnvironmentUrls, EventStream } from 'Roblox';
import { authenticatedUser } from 'header-scripts';
import accountSettingsModule from '../accountSettingsModule';
import twoStepVerificationConstants from '../constants/twoStepVerificationConstants';

function twoStepVerificationService($q, httpService, errorMessageService, robuxSpendTwoStepResources, languageResource, systemFeedbackService) {
  'ngInject';

  let metadataCache = null;

  const twoStepVerificationErrorMapper = errorMessageService.createErrorMapper(
    {
      [twoStepVerificationConstants.errorCodes.internal.tooManyRequests]: languageResource.get(
        'Message.Error.TooManyRequests'
      ),
      [twoStepVerificationConstants.errorCodes.internal.invalidEmail]: languageResource.get(
        'Message.Error.NoVerifiedEmail'
      ),
      [twoStepVerificationConstants.errorCodes.internal.invalidPassword]: languageResource.get(
        'Message.Error.Email.IncorrectPassword'
      ),
      [twoStepVerificationConstants.errorCodes.internal.featureDisabled]: languageResource.get(
        'Response.FeatureDisabled'
      ),
      [twoStepVerificationConstants.errorCodes.internal.pinLocked]: languageResource.get(
        'Message.Error.Email.PinLocked'
      ),
      [twoStepVerificationConstants.errorCodes.internal.invalidSetupToken]: languageResource.get(
        'Response.Dialog.AuthenticatorSessionExpired'
      ),
      [twoStepVerificationConstants.errorCodes.internal.invalidCode]: languageResource.get(
        'Response.Dialog.InvalidCodeError'
      )
    },
    languageResource.get('Message.Error.Default')
  );

  return {
    getMetadata() {
      return $q((resolve, reject) => {
        if (metadataCache) {
          resolve(metadataCache);
          return;
        }

        httpService
          .httpGet({
            url: `${EnvironmentUrls.twoStepVerificationApi}/v1/metadata`
          })
          .then(metadata => {
            metadataCache = metadata;
            resolve(metadata);
          })
          .catch(err => {
            const errorCode = httpService.getPrimaryApiErrorCode(
              err,
              twoStepVerificationConstants.errorCodes.api
            );
            reject({
              code: errorCode,
              message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
            });
          });
      });
    },

    getTwoStepVerificationConfiguration() {
      return $q((resolve, reject) => {
        httpService
          .httpGet({
            url: `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/configuration`
          })
          .then(configuration => {
            resolve(configuration);
          })
          .catch(err => {
            const errorCode = httpService.getPrimaryApiErrorCode(
              err,
              twoStepVerificationConstants.errorCodes.api
            );
            reject({
              code: errorCode,
              message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
            });
          });
      });
    },

    enableEmailTwoStepVerification(password) {
      return $q((resolve, reject) => {
        httpService
          .httpPost({
            url: `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/configuration/email/enable`
          },
          {
            password
          })
          .then(() => {
            resolve();
          })
          .catch(err => {
            const errorCode = httpService.getPrimaryApiErrorCode(
              err,
              twoStepVerificationConstants.errorCodes.api
            );
            reject({
              code: errorCode,
              message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
            });
          });
      });
    },

    enableAuthenticator(password) {
      return $q((resolve, reject) => {
        httpService
          .httpPost(
            {
              url: `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/configuration/authenticator/enable`
            },
            {
              password
            }
          )
          .then(resolve)
          .catch(err => {
            const errorCode = httpService.getPrimaryApiErrorCode(
              err,
              twoStepVerificationConstants.errorCodes.api
            );
            reject({
              code: errorCode,
              message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
            });
          });
      });
    },

    enableVerifyAuthenticator(setupToken, code, password) {
      return $q((resolve, reject) => {
        httpService
          .httpPost(
            {
              url: `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/configuration/authenticator/enable-verify`
            },
            {
              setupToken,
              code,
              password
            }
          )
          .then(() => {
            resolve();
          })
          .catch(err => {
            const errorCode = httpService.getPrimaryApiErrorCode(
              err,
              twoStepVerificationConstants.errorCodes.api
            );

            if (errorCode === twoStepVerificationConstants.errorCodes.internal.alreadyEnabled) {
              resolve();
            } else {
              reject({
                code: errorCode,
                message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
              });
            }
          });
      });
    },

    disableTwoStepVerification(mediaType, password) {
      return $q((resolve, reject) => {
        if (!password) {
          reject(twoStepVerificationConstants.errorCodes.internal.invalidPassword);
          return;
        }

        const urlConfig = {};
        switch (mediaType) {
          case twoStepVerificationConstants.mediaTypes.Email:
            urlConfig.url = `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/configuration/email/disable`;
            break;
          case twoStepVerificationConstants.mediaTypes.Authenticator:
            urlConfig.url = `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/configuration/authenticator/disable`;
            break;
          default:
            reject(twoStepVerificationConstants.errorCodes.internal.invalidMediaType);
        }

        httpService
          .httpPost(urlConfig, {
            password
          })
          .then(() => {
            resolve();
          })
          .catch(err => {
            const errorCode = httpService.getPrimaryApiErrorCode(
              err,
              twoStepVerificationConstants.errorCodes.api
            );
            reject({
              code: errorCode,
              message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
            });
          });
      });
    },

    getRecoveryCodesStatus() {
      return $q((resolve, reject) => {
        httpService
          .httpGet({
            url: `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/recovery-codes`
          })
          .then(response => {
            resolve(response);
          })
          .catch(err => {
            const errorCode = httpService.getPrimaryApiErrorCode(
              err,
              twoStepVerificationConstants.errorCodes.api
            );
            reject({
              code: errorCode,
              message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
            });
          });
      });
    },

    generateRecoveryCodes(password) {
      return $q((resolve, reject) => {
        if (!password) {
          reject(twoStepVerificationConstants.errorCodes.internal.invalidPassword);
          return;
        }

        httpService
          .httpPost(
            {
              url: `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/recovery-codes/regenerate`
            },
            {
              password
            }
          )
          .then(response => {
            resolve(response.recoveryCodes);
          })
          .catch(err => {
            const errorCode = httpService.getPrimaryApiErrorCode(
              err,
              twoStepVerificationConstants.errorCodes.api
            );
            reject({
              code: errorCode,
              message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
            });
          });
      });
    },

    getStatusSpendFriction() {
      return $q((resolve, reject) => {
        httpService
            .httpGet(
                {
                  url: `${EnvironmentUrls.economyApi}/v2/spend-friction/two-step-verification/status`
                },
                {}
            )
            .then(response => {
              resolve(response);
            })
            .catch(err => {
              const errorCode = httpService.getPrimaryApiErrorCode(
                  err,
                  twoStepVerificationConstants.errorCodes.api
              );
              reject({
                code: errorCode,
                message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
              });
            });
      });
    },

    getStatusTradeFriction() {
      return $q((resolve, reject) => {
        httpService
            .httpGet(
                {
                  url: `${EnvironmentUrls.tradesApi}/v1/trade-friction/two-step-verification/status`
                },
                {}
            )
            .then(response => {
              resolve(response);
            })
            .catch(err => {
              const errorCode = httpService.getPrimaryApiErrorCode(
                  err,
                  twoStepVerificationConstants.errorCodes.api
              );
              reject({
                code: errorCode,
                message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
              });
            });
      });
    },
    
    generateChallengeForSpend() {
      return $q((resolve, reject) => {
        httpService
            .httpPost(
                {
                  url: `${EnvironmentUrls.economyApi}/v2/spend-friction/two-step-verification/generate`
                },
                {}
            )
            .then(response => {
              resolve(response);
            })
            .catch(err => {
              const errorCode = httpService.getPrimaryApiErrorCode(
                  err,
                  twoStepVerificationConstants.errorCodes.api
              );
              reject({
                code: errorCode,
                message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
              });
            });
      });
    },

    generateChallengeForTrade() {
      return $q((resolve, reject) => {
        httpService
            .httpPost(
                {
                  url: `${EnvironmentUrls.tradesApi}/v1/trade-friction/two-step-verification/generate`
                },
                {}
            )
            .then(response => {
              resolve(response);
            })
            .catch(err => {
              const errorCode = httpService.getPrimaryApiErrorCode(
                  err,
                  twoStepVerificationConstants.errorCodes.api
              );
              reject({
                code: errorCode,
                message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
              });
            });
      });
    },

    verifyChallengeCode(challengeId, code) {
      return $q((resolve, reject) => {
        httpService
            .httpPost(
                {
                  url: `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/challenges/email/verify`
                },
                {
                  challengeId,
                  actionType: robuxSpendTwoStepResources.actionTypes.robuxSpend,
                  code
                }
            )
            .then(response => {
              resolve(response.verificationToken);
            })
            .catch(err => {
              const errorCode = httpService.getPrimaryApiErrorCode(
                  err,
                  twoStepVerificationConstants.errorCodes.api
              );
              reject({
                code: errorCode,
                message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
              });
            });
      });
    },

    resendEmailCode(challengeId) {
      return $q((resolve, reject) => {
        httpService
            .httpPost(
                {
                  url: `${EnvironmentUrls.twoStepVerificationApi}/v1/users/${authenticatedUser.id}/challenges/email/send-code`
                },
                {
                  challengeId: challengeId,
                  actionType: robuxSpendTwoStepResources.actionTypes.robuxSpend
                }
            )
            .then(() => {
              resolve();
            })
            .catch(err => {
              const errorCode = httpService.getPrimaryApiErrorCode(
                  err,
                  twoStepVerificationConstants.errorCodes.api
              );
              reject({
                code: errorCode,
                message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
              });
            });
      });
    },

    redeemVerificationChallengeForSpend(challengeId, verificationToken) {
      return $q((resolve, reject) => {
        httpService
            .httpPost(
                {
                  url: `${EnvironmentUrls.economyApi}/v2/spend-friction/two-step-verification/redeem`
                },
                {
                  challengeToken: challengeId,
                  verificationToken
                }
            )
            .then((success) => {
              resolve(success);
            })
            .catch(err => {
              const errorCode = httpService.getPrimaryApiErrorCode(
                  err,
                  twoStepVerificationConstants.errorCodes.api
              );
              reject({
                code: errorCode,
                message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
              });
            });
      });
    },

    redeemVerificationChallengeForTrade(challengeId, verificationToken) {
      return $q((resolve, reject) => {
        httpService
            .httpPost(
                {
                  url: `${EnvironmentUrls.tradesApi}/v1/trade-friction/two-step-verification/redeem`
                },
                {
                  challengeToken: challengeId,
                  verificationToken
                }
            )
            .then((success) => {
              resolve(success);
            })
            .catch(err => {
              const errorCode = httpService.getPrimaryApiErrorCode(
                  err,
                  twoStepVerificationConstants.errorCodes.api
              );
              reject({
                code: errorCode,
                message: twoStepVerificationErrorMapper.getErrorMessage(errorCode)
              });
            });
      });
    },
    
    sendFrictionEvent(buttonName) {
      if (EventStream && EventStream.SendEvent) {
        EventStream.SendEvent(
          robuxSpendTwoStepResources.events.frictionEventType,
          robuxSpendTwoStepResources.events.twoStepVerificationCtx,
          { btn: buttonName }
        );
      }
    }
  };
}

accountSettingsModule.factory('twoStepVerificationService', twoStepVerificationService);
export default twoStepVerificationService;
