import accountSettingsModule from '../accountSettingsModule';
import twoStepVerificationConstants from '../constants/twoStepVerificationConstants';

function accountSettingsModalTwoStepController(
  $scope,
  $uibModalInstance,
  modalData,
  twoStepVerificationService
) {
  'ngInject';

  (function setTwoStepDisablingWarning() {
    $scope.disableWarning = 'Response.Dialog.TwoStepDisableWarning';

    if (modalData.enabledMediaTypes.length > 1) {
      if (modalData.mediaType === twoStepVerificationConstants.mediaTypes.Email) {
        $scope.disableWarning = 'Response.Dialog.TwoStepDisableWarningEmail';
      } else if (modalData.mediaType === twoStepVerificationConstants.mediaTypes.Authenticator) {
        $scope.disableWarning = 'Response.Dialog.TwoStepDisableWarningAuthenticator';
      }
    }
  })();

  $scope.layout = {
    password: '',
    errorMessage: '',
    disableTwoStepWarningMessage: $scope.disableWarning,
    debounce: false
  };

  $scope.canSubmit = () => {
    return $scope.layout.password && !$scope.layout.errorMessage && !$scope.layout.debounce;
  };

  $scope.clearErrorMessage = () => {
    $scope.layout.errorMessage = '';
  };

  $scope.dismiss = () => {
    $uibModalInstance.dismiss();
  };

  $scope.submit = form => {
    if ($scope.layout.debounce) {
      return;
    }

    if (form.$valid) {
      form.$setPristine();

      $scope.clearErrorMessage();
      $scope.layout.debounce = true;

      twoStepVerificationService
        .disableTwoStepVerification(modalData.mediaType, $scope.layout.password)
        .then(() => {
          $uibModalInstance.close(true);
        })
        .catch(error => {
          $scope.layout.errorMessage = error.message;
          $scope.layout.debounce = false;
        });
    }
  };
}

accountSettingsModule.controller(
  'accountSettingsModalTwoStepController',
  accountSettingsModalTwoStepController
);
export default accountSettingsModalTwoStepController;
